@import '../mixins';

.hero {
    &__body {
        padding-top: 120px;
        padding-left: 126px;

        &__heading {
            color: #FFF;
            font-family: Inter Tight;
            font-size: 48px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            letter-spacing: 0.96px;
        }

    }

    .swiper-pagination-bullet {
        width: 71px;
        height: 6px;
        border-radius: 0;
        background: rgba(255, 255, 255, 0.70);
        opacity: 1;

        &:not(:last-child) {
            margin-right: 24px !important;
        }
    }

    .swiper-pagination-bullet-active {
        background: linear-gradient(to right, #3F3F3E 0, #3F3F3E 70%, #FFFFFFB2 70%, #FFFFFFB2 100%);
    }
}